<template>
<div class='clearfix'>
  <div class="gameListCon inner">
    <game-list :GameList ='gameList' />
    <button class="watchMore" @click="loadData()">查看更多</button>
  </div>
</div>
</template>
<script>
import gameList from '@/components/gameList'
export default {
  components: {
    gameList
  },
  data: function () {
    return {
      name: '123',
      gameList: [1, 2, 3]
    }
  },
  methods: {
    loadData () {
      console.log(1)
      for (var i = 0; i < 4; i++) {
        this.gameList.push(i)
      }
    }
  }
}
</script>
<style lang="scss" scoped>
.clearfix{
  background: #f5f5f5;
}
.gameListCon{
  padding: 0 20px 70px 0;
  position: relative;
}
.watchMore{
  margin: 23px 0;
  position: absolute;
  left: 50%;
  margin-left: -48px;
  color: #333333;
  background: #ddd;
  border: none;
  line-height: 35px;
  font-size: 14px;
  width: 96px;
}
</style>



