<template>
 <transition name="el-fade-in-linear">
  <el-row>
    <el-col v-for="(v,i) in GameList" :key="i" :xs="24" :sm="12" :md="8" :lg="8" :xl="1" class="gameLi">
      <div class="gameIntro">
        <div class="logoCon">
          <router-link title='title' :to="{path:'/detail', query:{'id':v.id}}" >
            <img src="../../../static/img/erzhan.jpg">
          </router-link>
        </div>
        <div class="intro">
          <div class="introview">
            <h4>
              <router-link title="title" :to="{path:'/detail', query:{'id':v.id}}" >
                <span>1231231231231231231231231</span>
              </router-link>
              <router-link class="viewBtn" :to="{path:'/detail', query:{'id':v.id}}" >查看</router-link>
            </h4>
          </div>
          <p>昂首调试地哦啊昂首调试地哦啊昂首调试地哦啊昂首调试地哦啊昂首调试地哦啊昂首调试地哦啊昂首调试地哦啊昂首调试地哦啊昂首调试地哦啊</p>
        </div>
        <span class="type">休闲益智</span>
      </div>
    </el-col>
  </el-row>
</transition>
</template>
<script>
export default {
  props: {
    GameList: {
      type: Array,
      default: () => []
    }
  },
  data: function () {
    return {

    }
  }
}
</script>
<style lang="scss" scoped>
.gameLi{
  .gameIntro{
    background: white;
    position: relative;
    height: 152px;
    margin: 20px 0 0 20px;
    box-shadow:#dedddd 0 2px 4px;
    padding: 10px 5px 0 10px;
    .logoCon{
      float: left;
      width: 68px;
      a{
        img{
          width: 68px;
        }
        &:hover{
          transition: all 1s;
          transform: scale(1.1);
        }
      }
    }
    .intro{
      padding: 3px;
      margin: 0 0 0 78px;
      .introview{
        height: 100%;
        h4{
          height: 26px;
          line-height: 26px;
          text-align: left;
          font-size: 15px;
          color: #333333;
        }
        a{
          span{
            color: #333333;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            display: block;
            max-width: 100px;
            float: left;
          }
        }
        a.viewBtn{
          float: right;
          width: 64px;
          color: white;
          background: #24aa42;
          text-align: center;
          font-size: 12px;
          height: 26px;
          line-height: 26px;
          border-radius: 4px;
        }
      }
      p{
        text-align: left;
        color: #999;
        font-size: 12px;
        padding-right: 3px;
        display: -webkit-box;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 3;
        overflow: hidden;
      }
    }
    .type{
      position: absolute;
      bottom: 0;
      right: 0;
      font-size: 12px;
      line-height: 19px;
      text-align: center;
      padding: 3px 12px 0;
      color: #999;
      background: #ebebeb;
    }
  }
}
</style>



